import React, { Component } from "react";
import { Router, Route, Switch, Redirect } from "react-router";
import { history } from "./history";
import Menu from "./navbar/menu";
//import PrivateRoute from "./PrivateRoute";
//Class(Container) Component
import UserDashboard from "./views/pages/UserDashboard/UserDashboard";

//Authentication
import Login from "./views/pages/authentication/Login";
import Register from "./views/pages/authentication/Register";
import forgotPassword from "./views/pages/authentication/ForgotPassword";
import resetPassword from "./views/pages/authentication/ResetPassword";

class Routes extends Component {
   render() {
      return (
         <div>
            <Router history={history}>
               <Menu />
               <Switch>
                  <Route
                     path="/basic-proj/user-dashboard"
                     component={UserDashboard}
                  />
                  <Route path="/basic-proj/login" component={Login} />
                  <Route path="/basic-proj/register" component={Register} />
                  <Route
                     path="/basic-proj/forgotpassword"
                     component={forgotPassword}
                  />
                  <Route
                     path="/basic-proj/resetpassword"
                     component={resetPassword}
                  />
               </Switch>
            </Router>
         </div>
      );
   }
}
export default Routes;
